		          var reg = /<img src="(.+?)\">/g
		          var list = []
		          var result = null
		          do {
		              result = reg.exec(str)
		              result && list.push(result[1])
		          } while (result)
				  if(list.length<=0){
					  return;
				  }
		          return list
